	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Crazy Cousins","date":"01 Dec 2002","slug":"/2002-12-01-crazy-cousins/","tags":"Y2002"},"html":"<p>I promised Christopher's cousins yesterday that I would post their picture on the Internet, and that they would become famous. Well, I can fulfil the first promise anyway. From left to right, Lennart, Jakob, Freya and Mareike</p>\n<p><img src=\"http://www.papascott.de/images/cousins1102.jpg\" border=\"0\" alt=\"Christopher cousin's, 30 Nov 2002\" /></p>"},"next":{"fields":{"title":"Counting with Christopher","date":"26 Nov 2002","slug":"/2002-11-26-counting-with-christopher/"}},"previous":{"fields":{"title":"The Den Beste Exception","date":"01 Dec 2002","slug":"/2002-12-01-the-den-beste-exception/"}}},{"node":{"fields":{"title":"Counting with Christopher","date":"26 Nov 2002","slug":"/2002-11-26-counting-with-christopher/","tags":"Y2002"},"html":"<p><em>Eins-Vier-Fünf-Sechs-Neun-Sechs-Acht</em> (One-Four-Five-Six-Nine-Six-Eight)</p>\n<p>OK, so he's not a genius. Yet. But it's an improvement from a couple months ago, when everything more than two was <em>Dreizehn-Neunzehn</em> (Thirteen-Nineteen).</p>"},"next":{"fields":{"title":"Muffin Man","date":"25 Nov 2002","slug":"/2002-11-25-muffin-man/"}},"previous":{"fields":{"title":"Crazy Cousins","date":"01 Dec 2002","slug":"/2002-12-01-crazy-cousins/"}}},{"node":{"fields":{"title":"Muffin Man","date":"25 Nov 2002","slug":"/2002-11-25-muffin-man/","tags":"Y2002"},"html":"<p>After an experience with small children in a crowded restaurant, <a href=\"http://www.yarinareth.net/caveatlector/archive/week_2002_11_24.html#e001092\">Dorothea</a> says she doesn't care about my kid. Well, I don't care about her peace and quiet in a public place. I guess that makes us even. Maybe she'd prefer that Christopher stay home and bake muffins. Well, then.</p>\n<p><img src=\"http://www.papascott.de/images/crhmuffin1102.jpg\" border=\"0\" alt=\"Christopher stays home and bakes muffins\" title=\"Christopher stays home and bakes muffins\" /></p>"},"next":{"fields":{"title":"5 Hours for a Lampshade","date":"24 Nov 2002","slug":"/2002-11-24-5-hours-for-a-lampshade/"}},"previous":{"fields":{"title":"Counting with Christopher","date":"26 Nov 2002","slug":"/2002-11-26-counting-with-christopher/"}}}],"pathPrefix":"","first":false,"last":false,"index":811,"pageCount":1101}}

